import React, { useState, useEffect } from 'react'
import './styles.scss'

import Form from './Form'
import WarnMsg from './WarnMsg'
import Output from './Output'
import { Charset } from './types'
import ColorSchemeSwitcher from './ColorSchemeSwitcher'

interface AppState {
	tradText: string
	simpText: string
	displayDuplicates: boolean
	primaryCharSet: Charset
}

// å‘è¡Œå‘åž‹ã€å¹²ç‡¥å¹²éƒ¨ã€å¤©å¹²åœ°æ”¯ã€çš‡åŽä¹‹åŽã€é¢„åœèåœã€
// æ‰æ˜¯å¤©æ‰ã€äº†è§£ä¸äº†ã€ä¸‘è§’å¥½ä¸‘ã€æ‰‹è¡¨è¡¨çŽ°

const initialAppState: AppState = {
	tradText: `åŒ—å†¥æœ‰é­šï¼Œå…¶åç‚ºé¯¤ã€‚
é¯¤ä¹‹å¤§ï¼Œä¸çŸ¥å…¶å¹¾ä»Ÿè£ä¹Ÿã€‚
åŒ–è€Œç‚ºé³¥ï¼Œå…¶åç‚ºéµ¬ã€‚
éµ¬ä¹‹èƒŒï¼Œä¸çŸ¥å…¶å¹¾ä»Ÿè£ä¹Ÿï¼Œæ€’è€Œé£›ï¼Œå…¶ç¿¼è‹¥åž‚å¤©ä¹‹é›²ã€‚
æ˜¯é³¥ä¹Ÿï¼Œæµ·é‹å‰‡å°‡å¾™æ–¼å—å†¥ã€‚`,
	simpText: `åŒ—å†¥æœ‰é±¼ï¼Œå…¶åä¸ºé²²ã€‚
é²²ä¹‹å¤§ï¼Œä¸çŸ¥å…¶å‡ åƒé‡Œä¹Ÿã€‚
åŒ–è€Œä¸ºé¸Ÿï¼Œå…¶åä¸ºé¹ã€‚
é¹ä¹‹èƒŒï¼Œä¸çŸ¥å…¶å‡ åƒé‡Œä¹Ÿï¼Œæ€’è€Œé£žï¼Œå…¶ç¿¼è‹¥åž‚å¤©ä¹‹äº‘ã€‚
æ˜¯é¸Ÿä¹Ÿï¼Œæµ·è¿åˆ™å°†å¾™äºŽå—å†¥ã€‚`,
	displayDuplicates: false,
	primaryCharSet: 'trad',
}

if (!localStorage.appState) {
	localStorage.appState = JSON.stringify(initialAppState)
}

let appState = {} as AppState

try {
	appState = JSON.parse(localStorage.appState) as AppState

	if (
		!([
			['tradText', 'string'],
			['simpText', 'string'],
			['displayDuplicates', 'boolean'],
			['primaryCharSet', 'string'],
		] as const).every(([prop, type]) => typeof appState[prop] === type)
	) {
		throw new TypeError()
	}
} catch (_e) {
	// empty or corrupted state - reinitialize

	appState = initialAppState
}

const App: React.FC = () => {
	const [formState, setFormState] = useState(appState)

	const { simpText, tradText, displayDuplicates, primaryCharSet } = formState

	const primaryText = primaryCharSet === 'simp' ? simpText : tradText
	const secondaryText = primaryCharSet === 'simp' ? tradText : simpText

	useEffect(() => {
		localStorage.appState = JSON.stringify(formState)
	}, [formState])

	return (
		<div className='container'>
			<ColorSchemeSwitcher />
			<h1>Traditional â‡„ simplified ruby-text generator</h1>

			<Form formState={formState} setFormState={setFormState} />
			{Array.from(primaryText).length !==
				Array.from(secondaryText).length && (
				<WarnMsg
					msg={
						'Length of simplified and traditional input are different â€” please check results.'
					}
				/>
			)}
			<h2>Output</h2>
			<Output
				primaryText={primaryText}
				secondaryText={secondaryText}
				displayDuplicates={displayDuplicates}
			/>
		</div>
	)
}

export default App
